@import "https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap";
@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap";
* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

:focus, :active, a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 14px;
  line-height: 1;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a:visited, a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

body {
  height: 100%;
  width: 100%;
  color: #fff;
  font-family: Open Sans, sans-serif;
  font-weight: 400;
}

a {
  color: #fff;
}

.none {
  display: none;
}

.wrapper {
  height: 100%;
  width: 100%;
}

.header {
  z-index: 11;
  width: 100%;
  height: 110px;
  background: #0d0d0d4d;
  position: absolute;
}

.header__container {
  height: 100%;
}

.container {
  box-sizing: content-box;
  max-width: 940px;
  margin: 0 auto;
  padding: 0 20px;
}

.header__row {
  opacity: 1;
  z-index: 2;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  align-self: flex-start;
  padding-top: 27px;
}

.header-logo-tablet, .header-logo-phone {
  display: none;
}

.header__list {
  text-align: center;
  display: flex;
}

.header__list li:not(:last-child) {
  margin-right: 78px;
}

.nav__item a {
  border-color: #fff;
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  transition: all .5s;
}

.nav__item a:hover {
  color: #d22856;
  border-color: #d22856;
  transition: all .5s;
}

.nav__item a:active {
  color: #fff;
  opacity: .3;
  border-color: #fff;
  transition: all .3s;
}

.header__burger {
  height: 24px;
  flex-direction: column;
  gap: 5px;
  display: none;
}

.bar {
  width: 50px;
  height: 4px;
  background-color: #fff;
  border-radius: 2px;
  transition: all .5s;
}

.header__burger.act .bar:nth-child(2) {
  transform: translate(0, -200px)scale(0);
}

.header__burger.act .bar:first-child {
  width: 30px;
  transform: translate(0, 8px)rotate(-45deg);
}

.header__burger.act .bar:last-child {
  width: 30px;
  transform: translate(0, -10px)rotate(45deg);
}

.menu-active {
  border-bottom: 2px solid #fff;
  padding-bottom: 5px;
}

.intro {
  z-index: 10;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 224px 0 130px;
  position: relative;
}

.intro__container {
  display: flex;
}

.intro-mobile {
  display: none;
}

.intro__content {
  max-width: 300px;
  height: 500px;
  text-align: center;
  z-index: 6;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.intro__title {
  margin-bottom: 50px;
}

.title {
  font-size: 50px;
  font-weight: 300;
  line-height: 68px;
}

.intro__download-button {
  width: 100%;
  border: 3px solid #fff;
  border-radius: 29px;
  margin-bottom: 70px;
  padding: 15px 0;
  font-size: 21px;
  font-weight: 700;
  line-height: 18px;
  transition: all .5s;
}

.intro__download-button:hover {
  color: #d22856;
  background-color: #fff;
}

.intro__download-button:active {
  color: #d2285530;
  background-color: #fff;
}

.intro__icons {
  gap: 35px;
  margin-bottom: 55px;
  display: flex;
}

.text {
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
}

.intro__hend {
  z-index: 5;
  position: absolute;
  bottom: 0;
  right: 0;
}

.information {
  padding-top: 80px;
}

.information__title {
  max-width: 780px;
  text-align: center;
  color: #000;
  margin: 0 auto 50px;
}

.information__background {
  background: url("grey background.a5274a62.png") 0 0 / cover no-repeat;
  padding-top: 6px;
}

.information__row {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.phone__row, .infortion__culumn-row {
  justify-content: space-between;
  display: flex;
}

.information__column {
  text-align: center;
  max-width: 252px;
  flex-direction: column;
  display: flex;
}

.column__phone {
  width: 252px;
  z-index: 0;
  justify-content: center;
  margin-bottom: 24px;
  display: flex;
  position: relative;
}

.column__phone:after {
  z-index: 12;
  content: "";
  width: 280px;
  height: 33px;
  background: #f2f2f2;
  display: block;
  position: absolute;
  bottom: -1px;
  left: -10px;
}

.column__phone:before {
  content: "";
  z-index: 12;
  width: 280px;
  height: 17px;
  opacity: .3;
  filter: blur(2px);
  background: linear-gradient(90deg, #0000 0%, #000 49.8%, #0000 93.23%);
  display: block;
  position: absolute;
  bottom: 24px;
  left: -10px;
}

.column__title {
  color: #000;
  margin-bottom: 30px;
}

.subtitle {
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;
}

.column__text {
  color: #000;
  font-weight: 400;
  line-height: 30px;
}

.clock {
  background: #f2f2f2;
  margin-bottom: 72px;
  padding: 73px 0 63px;
}

.clock__content {
  gap: 70px;
  display: flex;
}

.clock__culmn {
  color: #000;
}

.clock__title {
  margin-bottom: 30px;
}

.clock__text {
  margin-bottom: 47px;
  line-height: 30px;
}

.clock__link a {
  color: #d22856;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  text-decoration-line: underline;
}

.slider-container {
  box-sizing: content-box;
  max-width: 940px;
  margin: 0 auto;
  position: relative;
}

.slider {
  max-width: 668px;
  margin: 0 auto;
  overflow: hidden;
}

.slider__list {
  transition: all .5s;
  display: flex;
}

.slider__content {
  box-sizing: content-box;
  color: #000;
  text-align: center;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
  display: flex;
}

.slider__icon {
  margin-bottom: 52px;
}

.slider__text {
  margin-bottom: 40px;
}

.slider__name {
  margin-bottom: 11px;
  font-size: 24px;
}

.arrow {
  background: #fff0;
}

.arrow-right {
  position: absolute;
  bottom: 151px;
  right: 20px;
}

.arrow-left {
  position: absolute;
  bottom: 151px;
  left: 20px;
}

.slider__pagination {
  height: 10px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

.pagination__circle {
  cursor: pointer;
  width: 6px;
  height: 6px;
  opacity: .3;
  background: #283645;
  border-radius: 50%;
  transition: all .5s ease-in-out;
}

.active {
  width: 10px;
  height: 10px;
  opacity: .8;
  background: #fff;
  border: 3px solid #283645;
}

.table__container {
  padding: 160px 20px 98px;
}

.table {
  background: url("background table.33273967.png") 0 0 / cover no-repeat;
}

table {
  width: 100%;
  border-collapse: collapse;
}

.table__head {
  height: 94px;
  width: 233px;
  background: #283645;
  border-bottom: none;
  border-right: 2px solid #fff;
}

thead tr th:first-child, thead tr th:last-child {
  border-right: none;
}

.table__head h1 {
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
}

.table__head p {
  font-size: 24px;
  font-weight: 300;
  line-height: 30px;
}

.table__body {
  color: #000;
  background-color: #fff;
  border-top: none;
}

.table__body td {
  border: 2px solid #e5e5e5;
  border-radius: 1px;
}

.cell {
  text-align: center;
  width: 234px;
  max-height: 59px;
}

.table__body .text {
  text-align: start;
  padding: 20px 0 20px 20px;
}

.contact-info__container {
  padding-top: 56px;
}

.contact-info__content {
  color: #000;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.contact-info__title {
  text-align: center;
  max-width: 462px;
  margin-bottom: 51px;
}

.contact-info__row {
  width: 100%;
  justify-content: center;
  gap: 100px;
  margin: 0 auto;
  display: flex;
}

.contact-info__culmn {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.contact-info__subtitle {
  margin-bottom: 20px;
  font-size: 18px;
}

.contact-info__inform {
  font-size: 24px;
  font-weight: 300;
  line-height: 33px;
}

.contact-info__inform a {
  color: #d22856;
  word-wrap: normal;
}

.map__cont {
  position: relative;
}

.map {
  height: 500px;
  width: 100%;
}

.map__triange {
  width: 100%;
  height: auto;
  position: absolute;
  top: -1px;
}

.map__triange img {
  width: 100%;
}

.footer {
  height: 200px;
}

.foote__container {
  height: 100%;
}

.footer__content {
  height: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.footer__icons-row {
  width: 300px;
}

.footer__icons {
  width: 200px;
}

.footer__logo img:first-child {
  display: none;
}

.footer__info {
  color: #000;
}

.footer__info span {
  color: #d22856;
}

@media (max-width: 920px) {
  .header {
    transition: all .5s;
    position: fixed;
  }

  .header.act {
    background: #283645;
  }

  .container {
    max-width: 620px;
  }

  .header-logo-main {
    display: none;
  }

  .header-logo-tablet {
    display: block;
  }

  .header__burger {
    display: flex;
  }

  .header__list {
    width: 100vw;
    background: #283645;
    border-top: 1px solid #000;
    flex-direction: column;
    transition: all .5s;
    position: absolute;
    top: -100vh;
    left: 0;
  }

  .header__list.act {
    top: 109px;
  }

  .header__list li:not(:last-child) {
    margin-right: 0;
  }

  .nav__item {
    text-transform: uppercase;
    background: #283645;
    border-bottom: .5px solid #000;
    padding: 20px 0;
    font-weight: 700;
    box-shadow: 0 1px #000, inset 0 1px #ffffff26;
  }

  .intro {
    padding: 224px 0 120px;
  }

  .title {
    font-size: 40px;
    line-height: 120%;
  }

  .intro__content {
    height: 100%;
  }

  .intro__title {
    max-width: 260px;
    margin-bottom: 39px;
  }

  .intro__download-button {
    margin-bottom: 50px;
  }

  .intro__icons {
    margin-bottom: 35px;
  }

  .intro__hend img {
    width: 100%;
    height: 545px;
  }

  .intro__information.text {
    font-size: 18px;
    font-weight: 300;
  }

  .information {
    padding-top: 58px;
  }

  .information__title {
    max-width: 588px;
    margin-bottom: 20px;
    line-height: 54px;
  }

  .column__phone.a, .b, .column__phone:after, .column__phone:before {
    display: none;
  }

  .column__phone {
    height: 482px;
    margin-bottom: 0;
  }

  .column__phone img {
    display: block;
  }

  .information__row {
    width: 100%;
    flex-direction: row;
  }

  .infortion__culumn-row {
    flex-direction: column;
    justify-content: center;
    gap: 40px;
  }

  .information__column {
    max-width: 310px;
    text-align: left;
  }

  .column__title {
    margin-bottom: 15px;
  }

  .subtitle {
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
  }

  .text {
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
  }

  .clock {
    background: #fff;
    margin-bottom: 109px;
    padding: 54px 0 0;
  }

  .clock__content {
    justify-content: space-between;
  }

  .clock__title {
    max-width: 200px;
    margin-bottom: 24px;
    font-size: 30px;
    font-weight: 700;
    line-height: 36px;
  }

  .clock__text {
    margin-bottom: 15px;
  }

  .clock__icon img {
    width: 238px;
    height: 276px;
  }

  .clock__culmn {
    width: 100%;
  }

  .slider {
    max-width: 620px;
  }

  .slider__icon, .arrow {
    display: none;
  }

  .slider__content {
    margin-bottom: 40px;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
  }

  .slider__pagination {
    margin-top: 0;
  }

  .table__container {
    padding-bottom: 70px;
  }

  .contact-info__container {
    padding-top: 40px;
  }

  .contact-info__inform {
    font-size: 24px;
    font-weight: 300;
    line-height: 33px;
  }

  .contact-info__row {
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 60px;
  }

  .contact-info__culmn:first-child {
    order: 1;
  }

  .contact-info__culmn:nth-child(2) {
    order: 3;
  }

  .contact-info__culmn:last-child {
    order: 2;
  }

  .map {
    height: 374px;
  }

  .footer {
    height: 127px;
  }

  .footer__logo img:first-child {
    display: block;
  }

  .footer__logo img:last-child {
    display: none;
  }
}

@media (max-width: 667px) {
  .container {
    max-width: 350px;
  }

  .header {
    height: 63px;
  }

  .header__list.act {
    top: 63px;
  }

  .header__logo {
    align-self: center;
    padding-top: 0;
  }

  .header-logo-tablet {
    display: none;
  }

  .header-logo-phone {
    display: block;
  }

  .intro {
    padding: 98px 0 0;
  }

  .intro__title {
    margin-bottom: 0;
  }

  .intro__container {
    position: relative;
  }

  .intro__content {
    width: 100%;
    margin: 0 auto;
    padding-bottom: 320px;
  }

  .intro .intro__download-button, .intro .intro__icons, .intro .intro__information {
    display: none;
  }

  .intro__hend {
    right: 30px;
  }

  .intro__hend img {
    width: auto;
    height: 286px;
  }

  .title {
    font-size: 24px;
    font-weight: 300;
    line-height: 120%;
  }

  .intro-mobile {
    background: #1d2631;
    padding: 28px 0 38px;
    display: block;
  }

  .intro-mobile__conteiner {
    text-align: center;
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .intro__download-button {
    margin-bottom: 38px;
  }

  .intro__icons {
    margin-bottom: 28px;
  }

  .information {
    padding-top: 46px;
  }

  .information__title {
    margin-bottom: 0;
  }

  .information__background {
    padding: 120px 0 0;
  }

  .information__title span {
    margin-top: 32px;
    display: block;
  }

  .information__row {
    flex-direction: column;
    align-items: center;
  }

  .phone__row {
    order: 2;
  }

  .column__phone {
    width: 157px;
    height: 277px;
  }

  .infortion__culumn-row {
    gap: 30px;
    margin-bottom: 40px;
  }

  .information__column {
    text-align: center;
  }

  .clock {
    display: none;
  }

  .slider {
    padding-top: 50px;
  }

  .slider__content {
    margin-bottom: 0;
  }

  .slider__subtitle {
    display: none;
  }

  .slider__text {
    order: 2;
  }

  .table {
    display: none;
  }

  .contact-info__title {
    margin-bottom: 40px;
  }

  .contact-info__row {
    flex-direction: column;
    justify-content: center;
    gap: 30px;
  }

  .contact-info__subtitle {
    margin-bottom: 10px;
  }

  .footer {
    height: 284px;
    padding: 40px 0 50px;
  }

  .footer__icons {
    width: inherit;
  }

  .footer__content {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }
}

.title-background {
  background-image: url("background.85916eae.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.container__new-title {
  color: #fff;
  text-align: center;
  padding-top: 180px;
  padding-bottom: 104px;
}

.container__new-subtitle {
  max-width: 570px;
  text-align: center;
  padding: 100px 0;
}

.panorama {
  margin-bottom: 50px;
}

.panorama__img img {
  width: 100%;
}

.panorama__content {
  color: #000;
  background-color: #f2f2f2;
  flex-direction: column;
  gap: 10px;
  padding: 31px 27px;
  display: flex;
}

.panorama-row {
  justify-content: space-between;
  display: flex;
}

.name {
  font-weight: 700;
}

.name span {
  margin-left: 5px;
  font-size: 12px;
  font-weight: 300;
}

.likes {
  color: #d22856;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
  display: flex;
}

.likes button {
  width: 13px;
  height: 12px;
  margin-left: 10px;
}

.new-text {
  color: #000;
  font-size: 14px;
  line-height: 24px;
}

.wall__container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.wall-block {
  max-width: 300px;
}

.wall-block__img {
  width: 300px;
  height: 250px;
}

.wall-block__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.wall-block__content {
  height: 190px;
  background-color: #f2f2f2;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;
  display: flex;
}

.wall-block__name {
  margin-bottom: 10px;
}

.wall-block__description {
  margin-bottom: 25px;
}

.pop-up__container {
  height: 100%;
  width: 100%;
  z-index: 100;
  opacity: 0;
  background-color: #0000004d;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: all 1s ease-in;
  display: flex;
  position: fixed;
}

.pop-up__container.activeted {
  opacity: 1;
  transition: all .5s ease-in;
}

.pop-up__content {
  background-color: #fff;
  border: 3px solid #d22856;
  border-radius: 30px;
  flex: 0 500px;
  justify-content: center;
  margin: 0 auto;
  padding: 50px;
  display: flex;
  position: relative;
}

.pop-up__form {
  width: 100%;
  color: #d22856;
  flex-direction: column;
  gap: 30px;
  display: flex;
}

.pop-up__block {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.pop-up__block label {
  font-size: 16px;
  font-weight: 400;
  line-height: 30px;
}

.pop-up__input {
  width: 100%;
  height: 50px;
  border: 2px solid #e5e5e5;
  border-radius: 1px;
  padding-left: 20px;
  transition: all .5s ease-in;
}

.pop-up__input:hover, .pop-up__input:focus {
  border-color: #283645;
}

.pop-up__registr a {
  color: #ba4665;
  border-bottom: 1px solid #d22856;
  transition: all .5s;
}

.pop-up__registr a:hover {
  color: #941b3b;
  border-color: #70132c;
}

.pop-up__button {
  height: 50px;
  color: #fff;
  background: #2aab6d;
  border: 2px solid #2aab6d;
  border-radius: 5px;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  transition: all .5s;
}

.pop-up__button:active {
  color: #ffffff4e;
  background: #1c9b5e;
  border-color: #1c9b5e;
}

.pop-up__button:hover {
  background: #1c9b5e;
  border-color: #1c9b5e;
}

.pop-up__cross {
  height: 30px;
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 15px;
}

.pop-up__cross:hover .cross__bar {
  width: 40px;
